.wrapper {
    display: grid;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    font-family: "Rubik";
    font-style: normal;
    font-weight: 500;
    background-color: white;
    padding-bottom: 5%;
    padding-top: 5%;
    margin-left: 15%;
    margin-right: 15%;
    margin-top: 4%;
    margin-bottom: 4%;
    position: relative;
    @media(max-width: 530px) {
        margin-left: 5%;
        margin-right: 5%;
        padding: 16px;
    }
    li {
        margin-bottom: 1rem;
        label {
            font-size: 18px;
            line-height: 21px;
            color: #094067;
            margin-bottom: 8px;
        }
        input {
            border: none;
            background: #D8EEFE;
            border-radius: 5px;
            outline: none;
            font-size: 18px;
            line-height: 21px;
            color: #094067;
            padding-top: 14px;
            padding-bottom: 15px;
            padding-left: 20px;
            width: 60%;
            padding-right: 16px;
        }
        button {
            outline: none;
            cursor: pointer;
            width: 151px;
            padding-left: 20px;
            padding-top: 14px;
            padding-bottom: 15px;
            border-radius: 5px;
            font-size: 18px;
            line-height: 21px;
            margin-bottom: 10px;
            border: 2px solid #5F6C7B;
        }
        .save {
            background: #EF4565;
            color: #FFFFFE;
            margin-right: 20px;
            border: none;
        }
        .cancel {
            color: #5F6C7B;
            border-radius: 5px;
            background: #FFFFFF;
            box-sizing: border-box;
        }
        .rmv {
            outline: none;
            border: none;
            background-color: white;
            font-weight: bolder;
            font-size: 22px;
            cursor: pointer;
            position: absolute;
            top: 2%;
            right: 6%;
            width: 0;
            height: 0;
        }
    }
}